import React, { useState, useEffect } from "react";
import axios from "axios";
import AddPersons from "./components/AddPersons";
import Persons from "./components/Persons";

const App = () => {
  const Url = "https://crud-app08.herokuapp.com/api/persons";
  const [persons, setpersons] = useState([]);
  const getPersons = () => {
    console.log("called");
    axios
      .get(Url)
      .then((res) => setpersons(res.data))
      .catch((err) => console.log(err));
  };
  useEffect(() => {
    getPersons();
  }, []);
  return (
    <div>
      <AddPersons getPersons={getPersons} Url={Url}/>
      <Persons persons={persons} getPersons={getPersons} Url={Url} />
    </div>
  );
};

export default App;
